import { useContext } from "react";
import styled, { css } from "styled-components";
import { GlobalContext } from "../../../entry/systemEntry";
import InstrumentSwitch from "./item/instrumentSwitch";
import QuantizeSwitch from "./item/quantizeSwitch";


import { GiViolin, GiGuitar, GiSaxophone, GiViola, GiTrumpet, GiClarinet } from 'react-icons/gi'


const MelodyHeader = () => {
    const { store, dispatcher } = useContext(GlobalContext);
    const state = store.tuneState;

    return (<>
        <_QuantizeSelector>
            <QuantizeSwitch value={16} />
            <QuantizeSwitch value={8} />
            <QuantizeSwitch value={4} />
        </_QuantizeSelector>
        <_InstrumentSelector>
            <InstrumentSwitch icon={<GiViolin />} name="violin" />
            <InstrumentSwitch icon={<GiGuitar />} name="e-guitar" />
            <InstrumentSwitch icon={<GiSaxophone />} name="saxphone" />
            <InstrumentSwitch icon={<GiViola />} name="viola" />
            <InstrumentSwitch icon={<GiTrumpet />} name="trumpet" />
            <InstrumentSwitch icon={<GiClarinet />} name="clarinet" />
            <InstrumentSwitch icon={<GiClarinet />} name="oboe" />
        </_InstrumentSelector>
    </>);
}

export default MelodyHeader;

const _QuantizeSelector = styled.div<{
}>`
    display: block;
    padding: 4px 4px 4px 0;
    background-color: #bbbbbb;
`;

const _InstrumentSelector = styled.div<{
}>`
    display: inline-block;
    /* padding: 4px 4px 4px 0; */
    background-color: #e4e4e486;
`;

