import { KeyboardEvent } from "react";
import { Dispatcher } from "../../../../../redux/dispatcher/dispatcher";
import { Store } from "../../../../../redux/store/store";

export const MelodyKeyDown = (e: KeyboardEvent, store: Store, dispatcher: Dispatcher) => {

    const config = store.userEnv.keyConfig.note;
    const keySwitch = store.scoreState.keySwitch;
    // alert(e.key);
    // alert(config.nextElement);

    const state = store.tuneState;

    switch (e.key) {
        case 'ArrowUp': {
        }
            break;
        case 'ArrowDown': {
        }
            break;
        case 'ArrowLeft': {
            state.focusIndex--;
            dispatcher.tune.setState(state);
        }
            break;
        case 'ArrowRight': {
            state.focusIndex++;
            dispatcher.tune.setState(state);
        }
            break;
        case '1': {
            state.quantize = 16;
            dispatcher.tune.setState(state);
        }
            break;
        case '2': {
            state.quantize = 8;
            dispatcher.tune.setState(state);
        }
            break;
        case '3': {
            state.quantize = 4;
            dispatcher.tune.setState(state);
        }
            break;
        case 'Delete': {
            const notesList = store.scoreData.notesList;
            notesList.splice(state.focusIndex, 1);
            dispatcher.tune.setNotesList(notesList);
            state.focusIndex --;
            dispatcher.tune.setState(state);
        }
            break;
    }
}

